import React from 'react';
import styles from './Logo.module.css';

const Logo = () => {
  return (
    <div className={styles.container}>
      <img src="img/hnv.jpg" alt="Your Image" width="600"/>
      <div className={styles.text}>
        <h2>  </h2>
        <h2>  Human and Vision   </h2>
      </div>
    </div>
  );
};

export default Logo;